  margin: auto;
  animation: 2.4s linear infinite rotate;
  position: fixed;
  inset: 0;
}

.white {
  opacity: 0;
  background: #fff;
  animation: 2.4s linear infinite flash;
  inset: 0;
}

.dot {
  width: 2.4em;
  height: 2.4em;
  border-radius: 100%;
  margin: auto;
  transition: all 1s;
  position: absolute;
}

.dot:nth-child(2) {
  background: #f44;
  animation: 2.4s linear infinite dotsY;
  top: 0;
  bottom: 0;
  left: 0;
}

.dot:nth-child(3) {
  background: #fb3;
  animation: 2.4s linear infinite dotsX;
  top: 0;
  left: 0;
  right: 0;
}

.dot:nth-child(4) {
  background: #9c0;
  animation: 2.4s linear infinite dotsY;
  top: 0;
  bottom: 0;
  right: 0;
}

.dot:nth-child(5) {
  background: #33b5e5;
  animation: 2.4s linear infinite dotsX;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  10% {
    width: 6.25em;
    height: 6.25em;
  }

  66% {
    width: 2.4em;
    height: 2.4em;
  }

  100% {
    width: 6.25em;
    height: 6.25em;
    transform: rotate(360deg);
  }
}

@keyframes dotsY {
  66% {
    opacity: .1;
    width: 2.4em;
  }

  77% {
    opacity: 1;
    width: 0;
  }
}

@keyframes dotsX {
